
<template>
  <div>
    <section class="cms-list-query">
      <div class="query-group">
        <label>店铺名称</label>
        <el-input v-model="queryParams.storeName" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>商家账号</label>
        <el-input v-model="queryParams.storeUsername" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <el-table :data="tableData" style="cursor: pointer;" ref='dataTable' @selection-change='getCheckItems' @row-click='checkRows' max-height='500'>
      <!-- 多选 -->
      <el-table-column width="60" type="selection" v-if="multiple">
      </el-table-column>
      <!-- 单选 -->
      <el-table-column width="60" align="center" v-else>
        <div slot-scope="scope">
          <el-radio :label="scope.$index" v-model="selectIndex">&nbsp;</el-radio>
        </div>
      </el-table-column>
      <el-table-column prop="storeName" label="店铺名称" min-width='160'></el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
     <div slot="footer" class="dialog-footer" style="text-align:right">
        <el-button type="primary" @click="determine" size="mini">确 定</el-button>
        <el-button type='info' @click="close" size="mini">取 消</el-button>
      </div>
  </div>
</template>
<script>
import { shopManageList } from '@/api/shop/shopManage.js'
import list from '@/mixins/list'
export default {
  mixins: [list],
  data() {
    return {
      multiple: false,
      dialogVisible: false,
      checkItems: [],
      selectIndex: '', // 全选状态，默认全不选
      queryParams: {
        storeName: '',
        storeGradeId: '',
        storeType: '',
        categoryId: '',
        startTime: '',
        endTime: '',
        enabled: true,
        storeUsername: '',
        page: 1,
        size: 10,
        status: 1
      }
    }
  },
  methods: {
    checkRows(row) {
      // 单击行选中
      if (this.multiple) {
        // 多选时
        this.$refs.dataTable.toggleRowSelection(row)
      } else {
        // 单选时
        this.selectIndex = this.tableData.indexOf(row)
        this.checkItems = [row]
      }
    },
    determine() {
      this.$emit('get', this.multiple ? this.checkItems : this.checkItems[0])
      this.selectIndex = ''
    },
    close() {
      this.selectIndex = ''
      this.$emit('close', true)
    }
  },
  created() {
    this.getTableData(shopManageList, this.queryParams)
  }

}
</script>

<style>
</style>




