<template>
  <div>
    <cms-breadcrumb to="/advert/position/list" type='list'>
      <li>{{$route.name}}-{{$route.query.name}}</li>
    </cms-breadcrumb>
    <div class="cms-list-layout">
      <cms-prompt>
        <li>显示系统使用的广告版位</li>
        <li> 可对版位内的广告进行管理</li>
      </cms-prompt>
      <!--广告列表消息 -->
      <el-row :gutter="25" style="margin-top:20px;">
        <el-col :span="12" v-for="(item,index) in advertList" :key="item.id">
          <div class="advert-item">
            <div class="advert-operate" v-if="!item.isEdit">
              <span class="el-icon-edit-outline" @click="changeEdit(index)"></span>
              <span class="el-icon-close" @click="deleteAdvert(item.id,index)"></span>
            </div>
            <div class="isEdit" v-else>
              <el-button type="primary" size="mini" @click="submitForm(item,index)" 
                class="advert-operate__btn">保存
              </el-button>
              <el-button type="info" size="mini" class="advert-operate__btn"
                @click="cancelForm(item,index)"  v-if="item.id">取消
              </el-button>
            </div>
            <el-row :gutter="20">
              <el-col :span="6">
                <div class="advert-item__pic" style="margin-top:10px;">
                  <cms-picture-zone :preview='false' @change="getImgUrl" :float="false" :index="index">
                    <div class="pic-view">
                      <img :src="$cms.filterUrl(item.resourcesSpaceData.url)" v-if="item.resourcesSpaceData">
                      <span v-show="!item.resourcesSpaceData.url" class="pic-icon">+</span>
                    </div>
                  </cms-picture-zone>
                </div>
              </el-col>
              <el-col :span="18">
                <div class="advert-item__info">
                  <div class="info-flex">
                    <label class="info__inner--label">广告标题</label>
                    <div class="info__inner">
                      <el-input v-model="item.title" v-if="item.isEdit"
                       style="min-width:80px; max-width:70%;" maxlength="20"></el-input>
                      <span v-else>{{item.title}}</span>
                    </div>
                  </div>
                  <div class="info-flex">
                    <label class="info__inner--label">打开方式</label>
                    <div class="info__inner">
                      <el-radio-group v-model="item.isTarget" v-if="item.isEdit">
                        <el-radio :label="true">新窗口打开</el-radio>
                        <el-radio :label="false">本页打开</el-radio>
                      </el-radio-group>
                      <span v-else>{{item.isTarget?'新窗口打开':'本页打开'}}</span>
                    </div>
                  </div>
                  <div class="info-flex">
                    <label class="info__inner--label">是否显示</label>
                    <div class="info__inner">
                      <cms-switch v-model="item.isShow" v-if="item.isEdit"></cms-switch>
                      <span v-else>{{item.isShow?'显示':'不显示'}}</span>
                    </div>
                  </div>
                  <div class="info-flex">
                    <label class="info__inner--label">跳转目标</label>
                    <div class="info__inner">
                      <div class="info__inner--desc">
                        {{item.linkType | filterType(item)}}
                      </div>
                      <el-button type="text" size="mini" v-if="item.isEdit" 
                      @click="showDataDialog(index)" class="fl set-btn">设置</el-button>
                    </div>
                  </div>
                </div>
              </el-col>
            </el-row>

          </div>
        </el-col>
      </el-row>
      <data-dialog ref="dataDialog" @change="getData"></data-dialog>
    </div>
  </div>
</template>
<script>
import {
  advertPositionGet,
  advertSave,
  advertUpdate,
  advertDelete
} from '@/api/marketing/index.js'
import dataDialog from './components/index'
import form from '@/mixins/form.js'
var defaultData = {
  positionName: '',
  positionCode: '',
  page: 1,
  size: 10
}
export default {
  mixins: [form],
  components: {
    dataDialog
  },
  data() {
    return {
      formData: Object.assign({}, defaultData),
      advertList: []
    }
  },
  filters: {
    filterType(type, items) {
      let str = ''
      switch (type) {
        case 1:
          str = '[商品] ' + items.product.name
          break
        case 2:
          str = '[店铺] ' + items.store.storeName
          break
        case 3:
          str = '[文章] ' + items.article.title
          break
        case 4:
          str = '[商品分类] ' + items.productCategory.cateName
          break
        case 5:
          str = '[URL] ' + items.linkUrl
          break
        default:
          break
      }
      return str
    }
  },
  methods: {
    addForm() {
      const params = {
        title: '广告',
        adPositionId: this.id,
        isTarget: true,
        isShow: true,
        isEdit: true,
        productId: '',
        articleId: '',
        storeId: '',
        productCateId: '',
        linkUrl: '',
        pictureId: '',
        resourcesSpaceData: {}
      }
      this.advertList.push(params)
    },
    cancelForm(data, index) {
      data.isEdit = false
    },
    submitForm(data, index) { // 保存
      if (data.pictureId === '') {
        this.$errorMessage('必须上传一个广告图片')
      } else if (data.title === '') {
        this.$errorMessage('广告标题不能为空')
      } else {
        if (data.id) {
          // 如果id存在就做修改操作
          advertUpdate(data).then(res => {
            if (res.code === 200) {
              this.advertList.splice(index, 1, res.data)
              this.$set(this.advertList[index], 'isEdit', false)
              this.$successMessage('修改成功')
            } else {
              this.$errorMessage(res.message)
            }
          })
        } else {
          advertSave(data).then(res => {
            if (res.code === 200) {
              // this.advertList.splice(index, 1, res.data)
              // // this.$set(this.advertList[index], res.data)
              // this.$set(this.advertList[index], 'isEdit', false)
              this.getAdvertList(this.id)
              this.$successMessage('保存成功')
              // this.addForm()
            } else {
              this.$errorMessage(res.message)
            }
          })
        }
      }
      //   if (
      //     data.productId === '' &&
      //     data.articleId === '' &&
      //     data.storeId === '' &&
      //     data.productCateId === '' &&
      //     data.linkUrl === ''
      //   ) {
      //     this.$errorMessage('必选选择一个跳转目标')
      //   } else {

      // }
    },
    showDataDialog(index) {
      // 显示跳转目标数据
      this.$refs.dataDialog.showDialog(index)
    },
    getData(data, index) { // 设置跳转目标
      switch (
        data.type // 根据不同的数据类型构造不同的数据
      ) {
        case 1:
          this.clearOldData(index)
          this.$set(this.advertList[index], 'linkType', data.type)
          this.$set(this.advertList[index], 'product', data)
          this.$set(this.advertList[index], 'productId', data.id)
          break
        case 2:
          this.clearOldData(index)
          this.$set(this.advertList[index], 'linkType', data.type)
          this.$set(this.advertList[index], 'store', data)
          this.$set(this.advertList[index], 'storeId', data.id)
          break
        case 3:
          this.clearOldData(index)
          this.$set(this.advertList[index], 'linkType', data.type)
          this.$set(this.advertList[index], 'article', data)
          this.$set(this.advertList[index], 'articleId', data.id)
          break
        case 4:
          this.clearOldData(index)
          this.$set(this.advertList[index], 'linkType', data.type)
          this.$set(this.advertList[index], 'productCategory', data)
          this.$set(this.advertList[index], 'productCateId', data.id)
          break
        case 5:
          this.clearOldData(index)
          this.$set(this.advertList[index], 'linkType', data.type)
          this.$set(this.advertList[index], 'linkUrl', data.linkUrl)
          break
        default:
          return null
      }
    },
    clearOldData(index) {
      // 清除指定项的原始数据
      this.$set(this.advertList[index], 'productId', '')
      this.$set(this.advertList[index], 'articleId', '')
      this.$set(this.advertList[index], 'storeId', '')
      this.$set(this.advertList[index], 'store', '')
      this.$set(this.advertList[index], 'productCateId', '')
      this.$set(this.advertList[index], 'linkUrl', '')
    },
    getImgUrl(data, index) {
      // 获取图片地址
      this.$set(this.advertList[index], 'resourcesSpaceData', data[0])
      this.$set(this.advertList[index], 'pictureId', data[0].id)
    },
    changeEdit(index) {
      // 切换编辑状态
      if (this.advertList[index].isEdit) {
        this.submitForm(this.advertList[index], index)
      } else {
        this.$set(this.advertList[index], 'isEdit', true)
      }
    },
    deleteAdvert(id, index) {
      if (id === undefined) {
        this.advertList.splice(index, 1)
      } else {
        advertDelete({ ids: [id] }).then(res => {
          if (res.code === 200) {
            this.$successMessage('删除成功')
            this.advertList.splice(index, 1)
            this.getAdvertList(this.id)
          } else {
            this.$errorMessage('删除失败')
          }
        })
      }
    },
    getAdvertList(id) { // 获取广告列表
      advertPositionGet({ id: id }).then(res => {
        if (res.code === 200) {
          this.formData = res.data
          this.advertList = res.data.sysAds
          this.advertList.forEach((v, i) => {
            this.$set(this.advertList[i], 'isEdit', false)
          })
          this.addForm()// 新增一条自定义的数据
        }
      })
    }
  },
  created() {
    this.getAdvertList(this.id)
  }
}
</script>

 <style lang="scss" scoped>
.advert-item {
  width: 100%;
  border: 1px solid #e0e0e0;
  border-radius: 4px;
  padding: 15px;
  margin-bottom: 15px;
  overflow: hidden;
  font-size: 14px;
  position: relative;
 
}
.advert-operate {
  position: absolute;
  width: 40%;
  height: 20px;
  right: 20px;
  top: 8px;
  text-align: right;
  z-index: 500;
  .el-icon-close {
    cursor: pointer;
    font-size: 16px;
  }
  .el-icon-edit-outline {
    cursor: pointer;
  }
  // &__btn {
  //   // position: absolute;
  //   // right: 25px;
  //   // top: 20px;
  //   // z-index: 500;
  // }
}
.info-flex {
  display: flex;
  align-items: center;
  height: 40px;
}
.info__inner {
  display: inline-block;
  width: calc(100% - 85px);
  margin-left: 10px;
  overflow: hidden;
  &--label {
    display: inline-block;
    width: 60px;
  }
  &--desc {
    float: left;
    max-width: 90%;
    height: 28px;
    line-height: 28px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}
.pic-view {
  width: 100%;
  height: 140px;
  border: 1px solid #e0e0e0;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  img {
    width: auto;
    height: auto;
    max-height: 100%;
    max-width: 100%;
  }
}
.pic-icon{
  font-size: 60px;
  color: #999;
}
.isEdit {
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 500;
}
</style>



