
<template>
  <div>
    <section class="cms-list-query">
      <div class="query-group">
        <label>商品名称</label>
        <el-input v-model="queryParams.name" @keyup.native.enter="query"></el-input>
      </div>

      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <el-table :data="tableData" style="cursor: pointer;" ref='dataTable' @selection-change='getCheckItems' 
     @row-click='checkRows' max-height='450'>
      <!-- 多选 -->
      <el-table-column width="60" type="selection" v-if="multiple">
      </el-table-column>
      <!-- 单选 -->
      <el-table-column width="60" align="center" v-else>
        <div slot-scope="scope">
          <el-radio :label="scope.$index" v-model="selectIndex">&nbsp;</el-radio>
        </div>
      </el-table-column>
      <el-table-column prop="name" label="商品名称" min-width='240'></el-table-column>
      <el-table-column prop="store.storeName" label="店铺名称" min-width='140'></el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
    <div slot="footer" class="dialog-footer" style="text-align:right">
      <el-button type="primary" @click="determine" size="mini">确 定</el-button>
      <el-button type='info' @click="close" size="mini">取 消</el-button>
    </div>
  </div>
</template>
<script>
import { productPage } from '@/api/product/product.js'
import list from '@/mixins/list'
export default {
  mixins: [list],
  data() {
    return {
      multiple: false,
      dialogVisible: false,
      checkItems: [],
      selectIndex: '', // 全选状态，默认全不选
      queryParams: {
        page: 1,
        size: 10,
        status: 3,
        name: '',
        categoryId: '',
        itemCode: '',
        storeId: '',
        sellGoodsSale: '',
        storeCategoryId: '',
        storeName: '',
        itemNumber: ''
      }
    }
  },
  methods: {
    showProductImg(data) {
      // 显示主图
      if (data.length > 0) {
        const item = data.filter(v => v.isCover === true)
        if (item[0].imageResouce) {
          return this.$cms.filterUrl(item[0].imageResouce.url)
        } else {
          return '/static/img/error.png'
        }
      } else {
        return '/static/img/error.png'
      }
    },
    checkRows(row) {
      // 单击行选中
      if (this.multiple) {
        // 多选时
        this.$refs.dataTable.toggleRowSelection(row)
      } else {
        // 单选时
        this.selectIndex = this.tableData.indexOf(row)
        this.checkItems = [row]
      }
    },
    determine() {
      this.$emit('get', this.multiple ? this.checkItems : this.checkItems[0])
      this.selectIndex = ''
    },
    close() {
      this.selectIndex = ''
      this.$emit('close', true)
    }
  },
  created() {
    this.getTableData(productPage, this.queryParams)
  }
}
</script>

<style>
</style>




