
<template>
  <div>
    <el-form ref="form" label-width="84px">
      <el-form-item label="选择分类">
        <el-cascader :options="options" :props="props" :change-on-select='true' :show-all-levels="false" v-model="parentIds" @change="checkRows" ref="cascader">
        </el-cascader>
      </el-form-item>
      <el-form-item label=" ">
        <el-button type="primary" @click="determine" size="mini">确 定</el-button>
        <el-button type='info' @click="close" size="mini">取 消</el-button>
      </el-form-item>
    </el-form>

  </div>
</template>
<script>
import { productCategoryTree } from '@/api/product/category.js'
import list from '@/mixins/list'
export default {
  mixins: [list],
  data() {
    return {
      multiple: false,
      dialogVisible: false,
      checkItems: [],
      selectIndex: '', // 全选状态，默认全不选
      parentIds: [],
      options: [],
      props: {
        value: 'id',
        label: 'cateName',
        children: 'childs',
        disabled: 'isAuth'
      }
    }
  },
  methods: {
    getMenuTree() {
      // 获取菜单节点
      productCategoryTree().then(res => {
        this.options = res.data
      })
    },
    checkRows(data) {
      const params = {
        id: data[data.length - 1],
        cateName: this.$refs.cascader.currentLabels[
          this.$refs.cascader.currentLabels.length - 1
        ]
      }
      this.checkItems = params
    },
    determine() {
      this.$emit('get', this.checkItems)
      this.parentIds = []
    },
    close() {
      this.parentIds = []
      this.$emit('close', true)
    }
  },
  created() {
    this.getMenuTree()
  }
}
</script>

<style>
</style>




