<template>
  <div>
    <slot>
    </slot>
    <el-dialog title="选择链接" width="80%" :visible="visible" :before-close="close">
      <div style="padding:10px 15px;">
        <el-tabs v-model="active">
          <el-tab-pane label="商品" name="1">
            <product-list @get="getProductData" @close="close"></product-list>
          </el-tab-pane>
          <el-tab-pane label="店铺" name="2" lazy>
            <store-list @get="getStoreData" @close="close"></store-list>
          </el-tab-pane>
          <el-tab-pane label="文章" name="3" lazy>
            <article-list @get="getArticleList" @close="close"></article-list>
          </el-tab-pane>
          <el-tab-pane label="商品分类" name="4" lazy>
            <category-list @get="getCategoryList" @close="close"></category-list>
          </el-tab-pane>
          <el-tab-pane label="固定地址" name="5" lazy>
            <div>
              <el-form ref="forms" label-width="85px">
                <el-form-item label="链接地址" prop="linkurl">
                  <el-input v-model="linkUrl" maxlength="100" type="url" ></el-input>
                  <span class="gray">请输入正确的url地址，必须以https://或http://开头</span>
                </el-form-item>
                <el-form-item label=" ">
                  <el-button type="primary" @click="getUrlData" size="mini">确 定</el-button>
                  <el-button type='info' @click="close" size="mini">取 消</el-button>
                </el-form-item>
              </el-form>        
            </div>
          </el-tab-pane>
        </el-tabs>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import storeList from './store'
import productList from './product'
import articleList from './article'
import categoryList from './category'
export default {
  components: {
    storeList,
    productList,
    articleList,
    categoryList
  },
  data() {
    return {
      active: '1',
      visible: false,
      index: '',
      linkUrl: ''
    }
  },
  methods: {
    showDialog(index) {
      // 开启选择框
      this.index = index
      this.visible = true
    },
    close() {
      this.visible = false
      this.linkUrl = ''
    },
    closeDialog(params) {
      // 关闭选择框
      this.$emit('change', params, this.index)
      this.visible = false
    },
    getStoreData(data) {
      // 获取店铺数据
      const params = {
        type: 2,
        id: data.id,
        storeName: data.storeName
      }
      this.closeDialog(params)
    },
    getProductData(data) {
      const params = {
        type: 1,
        id: data.id,
        name: data.name
      }
      this.closeDialog(params)
    },
    getArticleList(data) {
      const params = {
        type: 3,
        id: data.id,
        title: data.title
      }
      this.closeDialog(params)
    },
    getCategoryList(data) {
      const params = {
        type: 4,
        id: data.id,
        cateName: data.cateName
      }
      this.closeDialog(params)
    },
    getUrlData() {
      const reg = /(http|https):\/\/[\w\-_]+(\.[\w\-_]+)+([\w\-\.,@?^=%&amp;:/~\+#]*[\w\-\@?^=%&amp;/~\+#])?/
      if (reg.test(this.linkUrl)) {
        const params = {
          type: 5,
          linkUrl: this.linkUrl
        }
        this.closeDialog(params)
      } else {
        this.$errorMessage('链接地址格式不正确')
      }
    }
  }
}
</script>

<style>
</style>



